* {
  box-sizing: border-box;
  font-family: 'PT Sans Narrow', sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
.cards {
    border: 4px rgb(99, 99, 99) solid;
    border-radius: 2px;
    width: 150px;
    height: 150px;
    margin: 1rem;
    position: relative;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    transition: all .2s ease-in-out;
  }
  
  .cards > .img-container {
    height: 100%;
    overflow: hidden;
    background:rgb(255, 255, 255);
  }
  
  .cards > .img-container > img {
    height: 100%;
  }
  
  .cards:hover {
    box-shadow: 8px 8px 20px rgb(56, 56, 56);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .cards:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
nav {
    width: 100%;
    font-size: 3rem;
    color: #fff;
    margin-bottom: 50px;
    background-color: rgb(56, 56, 56);
    height: auto;
    font-family: 'PT Sans Narrow', sans-serif;
  }
  
  a {
    color: #ffffff;
  }
  
  a:hover {
    color: #002366;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
    /* text-align: center; */
  }
  
  ul {
    /* padding-left: 0px; */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  @media only screen and (max-width: 750px) {
    #current-score, #top-score {
      font-size: 2rem;
    }
  }
.wrapper {
    background: rgb(175, 175, 175);
    background-image: url(/static/media/banner.26755070.jpg);
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
    padding-bottom: 3.125rem;
  }
.title {
    width: 100%;
    text-align: right;
    font-size: 2rem;
    color: #ffffff;
    margin-bottom: 50px;
    padding-left: 3%;
    padding-right: 3%;  
  }
.title{
  width: 100%;
  text-align: center;
  margin-bottom: 15rem;
}

